
:root {
  --primary: #4a6fa5;
  --dark: #343a40;
  --bg-light: #ffffff;
  --shadow-dark: rgba(174, 174, 192, 0.6);
  --shadow-light: rgba(255, 255, 255, 0.7);
}

.navbar {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background: var(--bg-light);
  border-radius: 0 0 15px 15px;
  z-index: 1000;
  transition: box-shadow 0.3s ease;
  box-shadow: 0 4px 10px var(--shadow-dark);

}

.modalOverlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.modal {
  background: var(--bg-light);
  padding: 2rem;
  border-radius: 20px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  text-align: center;
  max-width: 400px;
  width: 90%;
}

.modalActions {
  display: flex;
  justify-content: space-around;
  margin-top: 1.5rem;
  gap: 1rem;
}


.logoImage {
  height: 2.9rem;
  width: 8rem;

}




.logo {
  display: flex;
  align-items: center;
  font-size: 1.8rem;
  font-weight: 700;
  color: var(--dark);
}

.logoLink {
  color: var(--dark);
  text-decoration: none;
}

.icon {
  margin-right: 0.5rem;
  color: var(--primary);
  transition: transform 0.3s ease;
}

.logo:hover .icon {
  transform: rotate(20deg);
}

.links,
.actions {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.link,
.btn {
  color: var(--dark);
  font-weight: 500;
  padding: 0.5rem 1rem;
  border: none;
  cursor: pointer;
  border-radius: 25px;
  background: var(--bg-light);
  transition: color 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 5px 5px 10px var(--shadow-dark), -5px -5px 10px var(--shadow-light);
  text-decoration: none;
}

.link:hover:not(.active),
.btn:hover:not(.active) {
  color: var(--primary);
}

/* Neomorphic inset effect for selected (active) tabs/buttons */
.link.active,
.btn.active {
  color: var(--primary);
  background: var(--bg-light);
  box-shadow: inset 5px 5px 10px var(--shadow-dark), inset -5px -5px 10px var(--shadow-light);
}

.homeIcon {
  font-size: 1.5rem;
  color: var(--dark);
  transition: color 0.3s;
}

.homeIcon:hover {
  color: var(--primary);
}

.greeting {
  font-weight: 500;
  color: var(--dark);
}


.toast {
  position: fixed;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  background: var(--bg-light);
  color: var(--text-primary);
  padding: 1rem 2rem;
  border-radius: 12px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.15);
  font-weight: 500;
  z-index: 10000;
  animation: fadeInOut 3s ease-in-out;
}

@keyframes fadeInOut {
  0% { opacity: 0; transform: translateX(-50%) translateY(20px); }
  10% { opacity: 1; transform: translateX(-50%) translateY(0); }
  90% { opacity: 1; }
  100% { opacity: 0; transform: translateX(-50%) translateY(20px); }
}
